@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800&display=swap");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  color: #5A5C69;
}

p {
  color: #5A5C69;
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Nunito", sans-serif;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.body-layout {
  background: #f4f5f9;
  margin: 0;
  padding: 0;
}
.body-layout header {
  display: flex;
  justify-content: space-between;
  height: 8rem;
  align-items: center;
  padding: 0rem 3.5rem;
  color: #6a6b6d;
}
.body-layout header a {
  color: #6a6b6d;
  font-weight: 600;
}
.body-layout header h2 {
  margin: 0;
  font-size: 3rem;
  width: 20%;
}

#menu-toggle {
  display: none;
}

.main-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
}
.main-menu .menu-responsive {
  display: inline-block;
  width: 70%;
}
.main-menu .menu-responsive label {
  display: none;
  text-align: right;
  margin-bottom: 1rem;
  font-weight: 700;
}
.main-menu form.search {
  width: 70%;
  background: #fff;
}
.main-menu form.search input, .main-menu form.search button {
  height: 4.6rem;
  outline: none;
  background: none;
  border: none;
}
.main-menu form.search input {
  width: 90%;
  padding: 0 1rem;
}
.main-menu form.search button {
  width: 9%;
}

.carousel {
  height: 70vh;
  margin-top: 1rem;
}
.carousel .carousel-item {
  height: 100%;
  width: 70%;
  margin-right: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.5rem;
  padding: 2rem;
  overflow: hidden;
}
.carousel .carousel-item img {
  width: 100%;
  height: auto;
}
.carousel .carousel-info {
  width: 60%;
  padding: 0 2.5rem;
}
.carousel .carousel-info h2 {
  margin: 0;
  margin-bottom: 1rem;
  font-weight: 600;
  font-size: 4rem;
  color: #FFFFFF;
  text-align: left;
}
.carousel .carousel-info p {
  margin-bottom: 2rem;
  color: #FFFFFF;
  line-height: 1.7;
  font-size: 1.6rem;
  font-weight: 300;
}
.carousel .carousel-info a {
  background: #FFFFFF;
  color: #6a6b6d;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
}
.carousel .carousel-image {
  width: 60%;
}
.carousel .carousel-item:first-child {
  background: #8995a5;
}
.carousel .carousel-item:nth-child(2) {
  background: #303133;
}
.carousel .carousel-item:nth-child(3) {
  background: #1daadf;
}

/* posiciones de las flechas*/
.carousel .flickity-prev-next-button.previous {
  left: 22rem !important;
}

.carousel .flickity-prev-next-button.next {
  right: 22rem !important;
}

.popular {
  margin: 0 3.5rem;
  margin-bottom: 5rem;
}
.popular h1 {
  margin: 3rem 0;
}

.single-item {
  width: 20%;
  margin-right: 1rem;
  background: #fff;
}
.single-item .img {
  height: 20rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.single-item .info {
  padding: 1rem;
}
.single-item .info h3, .single-item .info p {
  text-align: center;
  margin-bottom: 1rem;
}
.single-item .info .item-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.single-item .info .item-flex .price h4 {
  font-size: 2rem;
}

.deals {
  background: #090b24;
  padding: 0 3rem;
}

.deals-inner {
  display: grid;
  grid-template-columns: 18% 82%;
  gap: 2rem 3rem;
}

.deal-carousel .single-item {
  width: 20% !important;
}

.timer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
}
.timer h2 {
  font-size: 4rem;
  margin-bottom: 1rem;
}
.timer h3 {
  font-size: 2rem;
}

/* posiciones de las flechas*/
.deal-carousel .flickity-prev-next-button.previous {
  left: 0rem !important;
}

.deal-carousel .flickity-prev-next-button.next {
  right: 22rem !important;
}

.card-brand {
  padding: 3rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0 3rem;
}

.single-card {
  height: 30rem;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 0.5rem;
  box-shadow: 0 0 0.8rem 0.2rem rgba(0, 0, 0, 0.3);
}

footer {
  background: #FFFFFF;
  padding: 3rem;
  display: grid;
  grid-template-columns: 22% 78%;
  gap: 2rem;
  color: #6a6b6d;
}
footer .brand h2 {
  margin: 0;
  font-size: 3rem;
  text-align: left;
}
footer .brand p {
  font-size: 1.6rem;
}

.footer-menu {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}

.menu-card {
  color: #6a6b6d;
  padding: 1rem;
}
.menu-card h3 {
  margin: 0;
  text-align: left;
  font-size: 2rem;
  margin-bottom: 0.7rem;
}
.menu-card a {
  display: block;
  color: #6a6a6d;
}

@media only screen and (max-width: 1000px) {
  .menu-responsive {
    width: 60%;
  }
}
@media only screen and (max-width: 768px) {
  #menu-toggle:checked ~ .main-menu::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background: rgba(0, 0, 0, 0.6);
  }

  #menu-toggle:checked ~ .main-menu .menu-responsive {
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 20;
  }

  #menu-toggle:checked ~ .main-menu .menu-responsive label {
    display: block;
  }

  .menu-responsive {
    position: absolute;
    top: -100%;
  }
}
.main-titulo {
  margin: 0;
  font-size: 2.75rem;
  font-weight: 400;
