.popup-img {
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
}
.popup-img .contenido-img {
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  padding: 1rem;
  width: 70%;
  transition: 0.3s ease all;
  transform: scale(0.7);
  opacity: 0;
}
@media (min-width: 768px) {
  .popup-img .contenido-img {
    width: 40%;
  }
}
.popup-img .contenido-img img {
  width: 100%;
}
.popup-img.active {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
.popup-img.active .contenido-img {
  opacity: 1;
  transform: scale(1);
}

