  color: #d1d3d2;
}
.nav-item-li .nav-link-li:hover .bx-nav-icon {
  color: #B7B9CC;
}
.nav-item-li .nav-link-li .span-li {
  position: absolute;
  transform: scale(0.7);
  transform-origin: top right;
  right: -0.1rem;
  margin-top: -0.9rem;
  color: #fff;
  background-color: #e74a3b;
  display: inline-block;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.35rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.nav-item-li .nav-link-li .img-perfil {
  width: 3.5rem;
  height: 3.5rem;
}
.nav-item-li .nav-link-li .name-link {
  display: none;
  margin: 0 0.8rem 0 0;
  color: #858796;
  font-weight: 400;
}
@media (min-width: 768px) {
  .nav-item-li .nav-link-li .name-link {
    display: block;
  }
}
.nav-item-li .mensajes {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transform: scale(0.7);
  transition: 0.3s;
  z-index: 1;
  padding: 0;
  border: none;
  overflow: hidden;
  top: 6rem;
  right: -8rem;
  width: calc(100vw - 15rem);
  border-radius: 0.5rem;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
}
.nav-item-li .mensajes.w-100 {
  width: calc(100vw - 5rem);
}
.nav-item-li .mensajes.mostrar {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
@media (min-width: 768px) {
  .nav-item-li .mensajes {
    width: 30rem;
    right: 0;
  }
  .nav-item-li .mensajes.w-100 {
    width: 30rem;
  }
}
.nav-item-li .mensajes h6 {
  margin: 0;
  background-color: #4e73df;
  border: 1px solid #4e73df;
  padding: 1rem 2.4rem;
  color: #fff;
  font-weight: 800;
  font-size: 1rem;
  text-transform: uppercase;
}
.nav-item-li .mensajes .mensaje-flex {
  display: flex;
  white-space: normal;
  padding: 0.8rem 2.4rem;
  border-left: 1px solid #e3e6f0;
  border-right: 1px solid #e3e6f0;
  border-bottom: 1px solid #e3e6f0;
  background-color: #FFFFFF;
  line-height: 1.3rem;
}
.nav-item-li .mensajes .mensaje-flex img {
  width: 4.2rem;
  height: 4.2rem;
  margin-right: 1rem;
}

.info-mensajes {
  padding: 0.5rem 0;
}
.info-mensajes .texto-truncado {
  max-width: 20rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 700;
  color: #3a3b45;
  text-align: inherit;
  font-size: 1.36rem;
  margin-bottom: 0.5rem;
}
.info-mensajes .autor {
  color: #b7b9cc;
  font-size: 80%;
  font-weight: 300;
}

.caja-perfil {
  visibility: hidden;
  opacity: 0;
  transform: scale(0.7);
  overflow: hidden;
  width: 15rem;
  position: absolute;
  right: 0.5rem;
  top: 6rem;
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  padding: 1rem 0;
  z-index: 1;
  transition: 0.3s;
}
.caja-perfil.quitar {
  visibility: hidden;
  opacity: 0;
}
.caja-perfil.mostrar {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}
.caja-perfil .perfil-item {
  display: block;
  padding: 0.4rem 2.3rem;
}
.caja-perfil .perfil-item:hover {
  background-color: #EAECF4;
}
.caja-perfil .perfil-item i {
  color: #d1d3d2;
}
.caja-perfil .perfil-item span {
  font-size: 1.4rem;
  margin-left: 0.5rem;
  color: #3a3b45;
  font-weight: 400;
}

.divider-perfil {
  height: 0;
  margin: 0.5rem 0;
  overflow: hidden;
  border-top: 1px solid #eaecf4;
}

.main {
  width: 100%;
  padding: 2rem;
  position: relative;
}

.logo {
  width: 5rem;
  margin-right: 0.5rem;
}

.contenedor-login {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(180deg, #4e73df 10%, #224abe 100%);
}
.contenedor-login .caja-contenido {
  border-radius: 1rem;
  overflow: hidden;
  width: 80%;
  background-color: #FFFFFF;
  display: grid;
  grid-template-columns: 1fr;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
}
.contenedor-login .caja-contenido .caja-imagen {
  display: none;
}
@media (min-width: 768px) {
  .contenedor-login .caja-contenido {
    width: 92rem;
    grid-template-columns: 1fr 1fr;
  }
  .contenedor-login .caja-contenido .caja-imagen {
    display: block;
  }
}
.contenedor-login .caja-contenido .caja-login {
  padding: 5rem;
}
.contenedor-login .caja-contenido .caja-login h1 {
  margin: 0 0 1rem 0;
  font-size: 3rem;
  font-weight: 400;
  color: #3A3B45;
}

.caja-login .formulario {
  margin: 0;
}
.caja-login .formulario .label:last-of-type {
  margin-top: 1rem;
}
.caja-login .formulario .espacio {
  height: 1rem;
  width: 100%;
}
.caja-login .formulario .input-text {
  padding: 2rem 1rem;
  font-size: 1.6rem;
  background-color: #FFFFFF;
  border-radius: 5rem;
  border: 1px solid #d1d3e2;
  width: 100%;
  color: #6e707e;
  transition: 0.3s;
}
.caja-login .formulario .input-text:focus {
  outline: none;
  border: 1px solid #b0ccdd;
  box-shadow: 0px 0px 0px 3px rgba(127, 201, 235, 0.29);
}
.caja-login .formulario .btn-login {
  cursor: pointer;
  margin-top: 2rem;
  padding: 2rem 1rem;
  border-radius: 5rem;
  width: 100%;
  background-color: #1cc88a;
  font-size: 1.6rem;
  border: none;
  color: #FFFFFF;
  transition: 0.3s;
}
.caja-login .formulario .btn-login:hover {
  background-color: #23aa79;
}